nav {
  display: flex;
  gap: 3rem;
  padding: 1rem 0;
}
nav ul {
  display: flex;
  gap: 2rem;
}
nav ul a {
  color: rgb(218, 179, 179);
}

.blog-title {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.blog-summary {
  font-family: "Times New Roman", Times, serif;
  font-style: italic;
  margin-bottom: 1.5rem;
}

*,
*::before,
*::after {
  padding: 0;
  margin: inherit 0;
  margin-top: 0;
  box-sizing: border-box;
}

.header {
  background-color: rgb(58, 50, 40);
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(238, 220, 198);
}

ul {
  list-style: none;
}

.container {
  width: min(90%, 60rem);
  margin: 0 auto;
}